    document.querySelectorAll("pre code").forEach(block => window.Prism.highlightElement(block));
    const a = { name: "zhangsan", age: 20, getName() { return this.name; } };
    console.log(a);
    const b = Queen.Tools.deepClone(a);
    a.name = "张三";
    a.getName = function() {
      return this.name + ",名字被修改了";
