
import { Vue } from "vue-class-component";
// import Queen from "../../src/component/Queen";
export default class SubmitBar extends Vue {
  public disabled = false;
  public popup = false;
  handleSubmit() {
    this.disabled = true;
    setTimeout(() => {
      this.Toast("提交成功");
      this.disabled = false;
    }, 2000);
  }
  handlePopup() {
    this.popup = !this.popup;
  }
  handleSubmit2() {
    this.Toast("提交成功");
  }
  handleSubmit3() {
    this.handlePopup();
    this.Toast("提交成功");
  }
}
